cambioclave(){

  if(this.username=='')
  {
    this.msj.showError3('Mensaje','Existe un problema con su dispositivo , no existe usuario');
    return;
  }
  if(!this.username)
  {
    this.msj.showError3('Mensaje','Existe un problema con su dispositivo , no existe usuario');
    return;
  }

  let prompt = this.alertCtrl.create({
    title:'Cambio de Contraseña',
    message: '',
    inputs: [
      {
        name: 'clave1',
        placeholder: 'Ingrese Contraseña nueva',
        type: 'password'

      },
      {
        name: 'clave2',
        placeholder: 'Ingrese Confirmación de Contraseña nueva',
        type: 'password'

      }
    ],
    buttons: [
      {
        text: 'Cancelar'
      },
      {
        text: 'Actualizar Contraseña',
        handler: todo => {

              if(todo){

                if(todo.clave1==todo.clave2)
                {
                  let credentials = {
                    idempresa:this.idempresa,
                    email         	:  this.username     	,
                    password: todo.clave1,
                    bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:'Actualiza',accion:'Cambia clave'}
                  };


                  this.authService.createAPISINTOKEN(credentials,'/api/auth/register2').then((result) => {
                    this.msj.showError3('Exito','Operación realizada con exito.');


                  }, (err) => {
                    this.msj.showError3('Advertencia','Email ya esta asociado una cuenta existente');

                  });

                }
                else
                {
                  this.msj.showError3('Advertencia','Contraseñas no coinciden.');


                }
                /*




  */

              }


        }
      }
    ]
  });

  prompt.present();








}

actualizar(){

  if(this.username=='')
  {
    this.msj.showError3('Error','Existe un problema con su dispositivo , no existe usuario');
    return;
  }
  if(!this.username)
  {
    this.msj.showError3('Error','Existe un problema con su dispositivo , no existe usuario');
    return;
  }


  let credentials = {
    idempresa:this.idempresa,
    email         	:  this.username     	,
    role:this.rolt,
    nombre        	: this.Form.get('nombre').value      	,
    cui 	: this.Form.get('cui').value	,
    direccion   	: this.Form.get('direccion').value   	,
    telefono    	: this.Form.get('telefono').value  	,
    foto    	:this.fotot    	,
    lenguaje    	: 'Español'   	,
    sexo    	: this.Form.get('sexo').value   	,
    estado:this.estadot,
    nov    	: this.Form.get('nov').value    	,
    unidad    	: this.unidad2 	,
    interno:this.internot,
    estadoemail:'1',
    codpersonal	: this.Form.get('codpersonal').value   	,
    usuarioup:this.username,
    fechanac: this.Form.get('fecha_nac').value   	,
    tiposuscriptor:this.tiposuscriptort,
    bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:'Actualiza',accion:'Actualiza perfil'}
  };

    this.authService.createReg( this.personalid ,credentials,'personals').then((result) => {
          this.translateService.get('ACTUALIZA').subscribe((value) => {
       //   this.msj.showError3('Exitos',value);
          this.storage.set('tiposus', this.tiposusnombre + ','+this.cobraparqueo+','+ this.Form.get('cui').value+ ','+this.idsuscriptort+ ',' + this.Form.get('nombre').value  +','+this.Form.get('nov').value+','+ this.rolt  +','+this.unidad2+ ',' + this.controlacceso + ',' +this.fotot);
          this.events.publish('nombre:cambia',this.Form.get('nombre').value);
          this.events.publish('foto:cambia',this.fotot);
      })
    }, (err) => {

          this.translateService.get('USER_ERROR').subscribe((value) => {
          this.msj.showError3('Error',value);
        })
    });


}


